import React from 'react';
import Router from 'react-routing/src/Router';
import http from './core/HttpClient';
import App from './components/App';
import IndexPage from './components/IndexPage';
import ContentPage from './components/ContentPage';
import ContactPage from './components/ContactPage';
import LoginPage from './components/LoginPage';
import RegisterPage from './components/RegisterPage';
import NotFoundPage from './components/NotFoundPage';
import ErrorPage from './components/ErrorPage';

// 路由规则
const router = new Router(on => {

  on('*', async (state, next) => {
    const component = await next();
    return component && <App context={state.context}>{component}</App>;
  });

  // 纯本地应用路由，比如要在cordova中打包

  on('/home/zengruiwen/WebstormProjects/JDM/build/templates/index.html', async () => <IndexPage />);

  on('/android_asset/www/index.html', async () => <IndexPage />);

  on('/android_asset/www/templates/index.html', async () => <IndexPage />);

  on('/templates/index.html', async () => <IndexPage />);

  on('/JDM/build/templates/index.html', async () => <IndexPage />);

  on('/JDM/build/index.html', async () => <IndexPage />);

  on('/index.html', async () => <IndexPage />);

  on('/', async () => <IndexPage />);

  on('/contact', async () => <ContactPage />);

  on('/login', async () => <LoginPage />);

  on('/register', async () => <RegisterPage />);

  // AJAX请求资源
  on('*', async (state) => {
    const content = await http.get(`/api/content?path=${state.path}`);
    return content && <ContentPage {...content} />;
  });

  on('error', (state, error) => state.statusCode === 404 ?
    <App context={state.context} error={error}><NotFoundPage /></App> :
    <App context={state.context} error={error}><ErrorPage /></App>
  );

});

export default router;



